<template lang="pug">
  .intro__canvas(ref="canvas")
</template>

<script>

import gsap, { Power3 } from 'gsap';
import { lerp } from '@/utils';
import {
  TextureLoader,
  LinearFilter,
  PlaneGeometry,
  ShaderMaterial,
  Mesh,
  Scene,
  PerspectiveCamera,
  WebGLRenderer,
  Vector2,
  DoubleSide,
  PlaneBufferGeometry,
  AdditiveBlending
} from "three";

import vertexShader from '@/misc/intro/vs';
import fragmentShader from '@/misc/intro/fs';

import myWorkVS from '@/misc/mywork/vs';
import myWorkFS from '@/misc/mywork/fs';

import target from '@/assets/images/wolf.png';
import original from '@/assets/images/myface-2.png';

const globalConfig = function() {
  this.blend = 0.0;
  this.bailProgress = 0.0;
  this.mouse = {
    x: -1,
    y: 0
  };
  this.flickTimer = 0.0;

  this.flickTimerTl = gsap.timeline({
    paused: true,
    repeat: -1
  });

  this.flickTimerTl.to(this, 0.1, {
    flickTimer: 1
  }, '+=5');

  this.flickTimerTl.to(this, 0.02, {
    flickTimer: 0
  });

  this.workBlend = 0.0;
  this.workBlendTl = gsap.timeline({
    paused: true
  });

  this.workBlendTl.to(this, 1.0, {
    workBlend: 1.0,
    ease: Power3.easeOut
  });

  this.projectList = [];
};



const data = new globalConfig();
window.canvasData = data;

class MyCanvas {
  constructor(idOrDOM) {
    this.init(idOrDOM);
  }
  loadImages() {
    let image1Url = original;
    let image2Url = target;
    let loader = new TextureLoader();

    const promises = [];

    this.workImages = [];

    data.projectList.forEach(project => {
      promises.push(
        new Promise(resolve => {
          loader.load(project.image, texture => {
            this.workImages.push(texture);
            texture.minFilter = LinearFilter;
            resolve();
          });
        })
      );
    });

    promises.push(
      new Promise(resolve => {
        loader.load(image1Url, texture => {
          this.image1 = texture;
          this.image1.minFilter = LinearFilter;
          resolve();
        });
      })
    );

    promises.push(
      new Promise(resolve => {
        loader.load(image2Url, texture => {
          this.image2 = texture;
          this.image2.minFilter = LinearFilter;
          resolve();
        });
      })
    );

    return Promise.all(promises);
  }
  async init(idOrDOM) {
    if (typeof idOrDOM == "string") {
      this.container = document.getElementById(idOrDOM);
    } else {
      this.container = idOrDOM;
    }

    this.state = {
      isActive: true,
      transitBlend: 0.0
    };

    this.time = 0;

    await this.loadImages();

    this.initScene();
    this.addObjectsToScene();

    this.addMyWorkImage();

    this.onWindowResize();
    this.animate();
  }
  addObjectsToScene() {
    const { clientWidth, clientHeight } = this.container;
    // Uniforms
    this.uniforms = {
      blend: { value: data.blend },
      bailProgress: {
        value: data.bailProgress
      },
      u_resolution: {
        type: "vec2",
        value: new Vector2(clientWidth, clientHeight)
      },
      time: {
        value: this.time
      },
      original: {
        type: "t",
        value: this.image1
      },
      target: {
        type: "t",
        value: this.image2
      },
      u_flickTimer: {
        value: data.flickTimer,
      },
      u_workImage: {
        type: "t",
        value: this.currentWorkImage
      },
      u_nextWorkImage: {
        type: 't',
        value: this.nextWorkImage
      },
      u_mouse: {
        type: "vec2",
        value: new Vector2(data.mouse.x, data.mouse.y)
      },
      u_workBlend: {
        value: data.workBlend,
      },
      u_transitBlend: {
        value: this.state.transitBlend
      }
    };

    this.geometry = new PlaneGeometry(clientWidth / clientHeight, 1, 64, 64);

    this.material = new ShaderMaterial({
      extensions: {
        derivatives: "#extension GL_OES_standard_derivatives : enable"
      },
      uniforms: this.uniforms,
      vertexShader,
      fragmentShader,
      depthTest: 1,
      transparent: 1
    });

    this.plane = new Mesh(this.geometry, this.material);
    this.scene.add(this.plane);
  }
  onWindowResize() {
    const { clientWidth, clientHeight } = this.container;

    this.renderer.setSize(clientWidth, clientHeight, true);

    if (window.innerWidth < 768) {
      this.renderer.setPixelRatio(0.9); // Performance
    } else {
      this.renderer.setPixelRatio(window.devicePixelRatio);
    }
  }
  initScene() {
    const { clientWidth, clientHeight } = this.container;

    this.currentWorkImage = this.workImages[0];
    this.nextWorkImage = this.workImages[1];

    this.scene = new Scene();
    this.camera = new PerspectiveCamera(
      53,
      clientWidth / clientHeight,
      0.1,
      100
    );

    this.renderer = new WebGLRenderer({
      alpha: 1,
      precision: 'mediump'
    });

    this.renderer.setClearColor(0x000000);
    this.camera.position.set(0, 0, 1);
    this.renderer.getContext().getExtension("OES_standard_derivatives");
    this.container.appendChild(this.renderer.domElement);
  }
  update() {
    this.time += 0.01;
    this.uniforms.time.value = this.time;
    this.uniforms.blend.value = data.blend;
    this.uniforms.bailProgress.value = data.bailProgress;
    this.uniforms.u_mouse.value.x = data.mouse.x;
    this.uniforms.u_mouse.value.y = data.mouse.y;
    this.uniforms.u_workBlend.value = data.workBlend;
    this.uniforms.u_transitBlend.value = this.state.transitBlend;
    this.uniforms.u_flickTimer.value = data.flickTimer;

    this.uniforms.u_workImage.value = this.currentWorkImage;
    this.uniforms.u_nextWorkImage.value = this.nextWorkImage;
  }
  animate() {
    window.requestAnimationFrame(this.animate.bind(this));

    if (!this.state.isActive) {
      return;
    }

    this.update();
    this.renderer.render(this.scene, this.camera);
  }
  unpause() {
    this.state.isActive = true;
  }
  pause(){
    this.state.isActive = false;
  }
  addMyWorkImage() {

    const geometry = new PlaneBufferGeometry(0.4 * 0.85, 0.3 * 0.85, 32, 32);
    const material = new ShaderMaterial({
      vertexShader: myWorkVS,
      fragmentShader: myWorkFS,
      uniforms: this.uniforms,
      side: DoubleSide,
      blending: AdditiveBlending
    });

    const workPlane = new Mesh(geometry, material);

    workPlane.position.y = 0.11;
    workPlane.position.z = 0.2;
    workPlane.rotation.x = -0.13;
    this.scene.add(workPlane);
  }
  transit(targetIdx) {
    this.nextWorkImage = this.workImages[targetIdx];
    this.nextWorkImage.needsUpdate = true;

    gsap.fromTo(this.state, 1.0, {
      transitBlend: 0,
    }, {
      transitBlend: 1.0,
      onComplete: () => {
        this.currentWorkImage = this.workImages[targetIdx];
        this.currentWorkImage.needsUpdate = true;
        this.state.transitBlend = 0;
      }
    })
  }
}

const onScrollCanvas = function() {
  const { currentSection, sectionProgress } = this.$store.state.scrollState;

  if (currentSection == 1) {
    data.bailProgress = sectionProgress;

    if (sectionProgress >= 0.6) {
      data.workBlendTl.reversed( 0 )
        .timeScale(1)
        .play()
      ;
    }
    else {
      data.workBlendTl.timeScale(5).reversed(1);
    }
  }

  if (currentSection > 1 || (currentSection >= 1 && sectionProgress >= 0.65)){
    this.stopFlickTimer();
  }
  else {
    this.runFlickTimer();
  }

  if (currentSection > 1 && sectionProgress >= 0.1) {
    data.workBlendTl.timeScale(5).reversed(1);
  }

  if (currentSection >= 2) {
    data.bailProgress = sectionProgress + 1.0;
  }
};

export default {
  name: "IntroCanvas",
  methods: {
    onMousemove(e) {
      const { x } = data.mouse;

      data.mouse = {
        x: lerp(e.clientX / window.innerWidth * 2 - 1, x , 0.9), // Lerpppp
        y: -(e.clientY / window.innerHeight) * 2 + 1
      }
    },
    stopFlickTimer() {
      if (!data.flickTimerTl) return;

      data.flickTimerTl.restart();
    },
    runFlickTimer() {
      data.flickTimerTl.play();
    },
    transit(toIndex) {
      this.canvas.transit(toIndex);
    },
    init() {
      data.projectList = this.projectList;

      this.canvas = new MyCanvas(this.$refs.canvas);

      gsap.fromTo(
        data,
        3,
        {
          blend: 0
        },
        {
          blend: 1.0
        }
      );

      this.runFlickTimer();
      document.addEventListener("resize", this.canvas.onWindowResize);
    }
  },
  data() {
    return {
      canvas: null,
      tl: null
    };
  },
  watch: {
    currentProject(newVal) {
      this.transit(newVal);
    },
    projectList(newVal) {
      if (!newVal.length) {
        return;
      }

      this.init();
    }
  },
  computed: {
    onScrollCanvasFunc() {
      return onScrollCanvas.bind(this);
    },
    currentProject() {
      return this.$store.state.projectState.currentProject;
    },
    projectList() {
      return this.$store.state.projectState.projectList;
    }
  },
  mounted() {
    document.addEventListener("mousemove", this.onMousemove);
    window.addEventListener("scroll", this.onScrollCanvasFunc);
  },
  beforeDestroy() {
    cancelAnimationFrame(this.canvas.animate);

    document.removeEventListener("mousemove", this.onMousemove);
    document.removeEventListener("resize", this.canvas.onWindowResize);
    window.removeEventListener("scroll", this.onScrollCanvasFunc);
  }
};
</script>
