import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

const DEFAULT_STATE = {
  scrollState: {
    currentSection: 0,
    sectionProgress: 0.0,
  },
  clickState: {
    clicked: false
  },
  projectState: {
    currentProject: 0,
    projectList: []
  }
}

const store = new Vuex.Store({
  state: DEFAULT_STATE,
  mutations: {
    setScrollState (state, payload) {
      state.scrollState = payload;
    },
    setClickState(state, payload) {
      state.clickState = payload;
    },
    setCurrentProject(state, payload) {
      state.projectState.currentProject = payload;
    },
    setProjectList(state, payload) {
      state.projectState.projectList = payload;
    }
  }
})

export default store
