export default `
uniform float time;
uniform sampler2D u_workImage;
uniform sampler2D u_nextWorkImage;
uniform vec2 u_resolution;
uniform float u_workBlend;
uniform float u_transitBlend;

varying vec2 vUv;

float hash(float n) { return fract(sin(n) * 1e4); }
float hash(vec2 p) { return fract(1e4 * sin(17.0 * p.x + p.y * 0.1) * (0.1 + abs(sin(p.y * 13.0 + p.x)))); }

float noise(vec2 x) {
  vec2 i = floor(x);
  vec2 f = fract(x);

  // Four corners in 2D of a tile
  float a = hash(i);
  float b = hash(i + vec2(1.0, 0.0));
  float c = hash(i + vec2(0.0, 1.0));
  float d = hash(i + vec2(1.0, 1.0));

  // Simple 2D lerp using smoothstep envelope between the values.
  // return vec3(mix(mix(a, b, smoothstep(0.0, 1.0, f.x)),
  //          mix(c, d, smoothstep(0.0, 1.0, f.x)),
  //          smoothstep(0.0, 1.0, f.y)));

  // Same code, with the clamps in smoothstep and common subexpressions
  // optimized away.
  vec2 u = f * f * (3.0 - 2.0 * f);
  return mix(a, b, u.x) + (c - a) * u.y * (1.0 - u.x) + (d - b) * u.x * u.y;
}

void main() {
  vec2 uv = gl_FragCoord.xy / u_resolution;
  vec4 texture = texture2D(u_workImage, vUv);

  vec4 blackTexel = vec4(vec3(0.0), 1.0);

  float nf = noise(uv * 150.0);
  vec2 directionVec = vec2(0.5, uv.y / 3.0) * (1.0 - u_workBlend);

  vec2 effectVec = nf * (1.0 - u_workBlend) * directionVec * 1.5;
  vec4 color = mix(blackTexel, texture2D(u_workImage, vUv + effectVec), u_workBlend);

  float nf2 = noise(uv * 181.0);
  vec2 directionTransitVec = vec2(0.5, uv.y / 3.0) * (1.0 - u_transitBlend);
  vec2 transitVec = nf2 * (1.0 - u_transitBlend) * directionTransitVec * 1.5;
  vec4 nextTexture = texture2D(u_nextWorkImage, vUv + transitVec);

  gl_FragColor = mix(color, nextTexture, u_transitBlend);
}
`
