export default `
  varying vec3 v_position;
  varying vec2 vUv;
  varying vec3 vColor;

  uniform sampler2D original;
  uniform sampler2D target;
  uniform sampler2D cloud;

  uniform float u_flickTimer;
  uniform float blend;
  uniform float time;
  uniform float bailProgress;
  uniform vec2 u_resolution;
  uniform vec2 u_mouse;

  #define PI 3.14159265358979323846
  #define HALFPI PI * 0.5
  #define DISSOLVE_COLOR vec4(0.0,0.0,0.0,1.0)
  #define IDLE_DISSOLVE_FACTOR 0.01

  float rand(vec2 c, float time){
    return fract(sin(dot(c.xy ,vec2(12.9898,78.233))) * 43758.5453 + time);
  }

  vec3 mod289(vec3 x) {
    return x - floor(x * (1.0 / 289.0)) * 289.0;
  }

  vec4 mod289(vec4 x) {
    return x - floor(x * (1.0 / 289.0)) * 289.0;
  }

  vec4 permute(vec4 x)
  {
    return mod(((x*34.0)+1.0)*x, 289.0);
  }

  vec4 taylorInvSqrt(vec4 r)
  {
    return 1.79284291400159 - 0.85373472095314 * r;
  }

  float snoise(vec3 v)
  {
    const vec2  C = vec2(1.0/6.0, 1.0/3.0);
    const vec4  D = vec4(0.0, 0.5, 1.0, 2.0);

    // First corner
    vec3 i  = floor(v + dot(v, C.yyy) );
    vec3 x0 =   v - i + dot(i, C.xxx) ;

    // Other corners
    vec3 g = step(x0.yzx, x0.xyz);
    vec3 l = 1.0 - g;
    vec3 i1 = min( g.xyz, l.zxy );
    vec3 i2 = max( g.xyz, l.zxy );

    //   x0 = x0 - 0.0 + 0.0 * C.xxx;
    //   x1 = x0 - i1  + 1.0 * C.xxx;
    //   x2 = x0 - i2  + 2.0 * C.xxx;
    //   x3 = x0 - 1.0 + 3.0 * C.xxx;
    vec3 x1 = x0 - i1 + C.xxx;
    vec3 x2 = x0 - i2 + C.yyy; // 2.0*C.x = 1/3 = C.y
    vec3 x3 = x0 - D.yyy;      // -1.0+3.0*C.x = -0.5 = -D.y

    // Permutations
    i = mod289(i);
    vec4 p = permute( permute( permute(
              i.z + vec4(0.0, i1.z, i2.z, 1.0 ))
            + i.y + vec4(0.0, i1.y, i2.y, 1.0 ))
            + i.x + vec4(0.0, i1.x, i2.x, 1.0 ));

    // Gradients: 7x7 points over a square, mapped onto an octahedron.
    // The ring size 17*17 = 289 is close to a multiple of 49 (49*6 = 294)
    float n_ = 0.142857142857; // 1.0/7.0
    vec3  ns = n_ * D.wyz - D.xzx;

    vec4 j = p - 49.0 * floor(p * ns.z * ns.z);  //  mod(p,7*7)

    vec4 x_ = floor(j * ns.z);
    vec4 y_ = floor(j - 7.0 * x_ );    // mod(j,N)

    vec4 x = x_ *ns.x + ns.yyyy;
    vec4 y = y_ *ns.x + ns.yyyy;
    vec4 h = 1.0 - abs(x) - abs(y);

    vec4 b0 = vec4( x.xy, y.xy );
    vec4 b1 = vec4( x.zw, y.zw );

    //vec4 s0 = vec4(lessThan(b0,0.0))*2.0 - 1.0;
    //vec4 s1 = vec4(lessThan(b1,0.0))*2.0 - 1.0;
    vec4 s0 = floor(b0)*2.0 + 1.0;
    vec4 s1 = floor(b1)*2.0 + 1.0;
    vec4 sh = -step(h, vec4(0.0));

    vec4 a0 = b0.xzyw + s0.xzyw*sh.xxyy ;
    vec4 a1 = b1.xzyw + s1.xzyw*sh.zzww ;

    vec3 p0 = vec3(a0.xy,h.x);
    vec3 p1 = vec3(a0.zw,h.y);
    vec3 p2 = vec3(a1.xy,h.z);
    vec3 p3 = vec3(a1.zw,h.w);

    //Normalise gradients
    vec4 norm = taylorInvSqrt(vec4(dot(p0,p0), dot(p1,p1), dot(p2, p2), dot(p3,p3)));
    p0 *= norm.x;
    p1 *= norm.y;
    p2 *= norm.z;
    p3 *= norm.w;

    // Mix final noise value
    vec4 m = max(0.6 - vec4(dot(x0,x0), dot(x1,x1), dot(x2,x2), dot(x3,x3)), 0.0);
    m = m * m;
    return 42.0 * dot( m*m, vec4( dot(p0,x0), dot(p1,x1),
                                  dot(p2,x2), dot(p3,x3) ) );
  }

  vec4 dissolve() {
    float p = 0.5; //Distance
    float a = 0.2 * PI; //Angle
    float s;

    s = smoothstep(-0.1, 1., sin(HALFPI * (1. - blend)) + sin(HALFPI * bailProgress));

    vec2 uv = vUv;

    float r = rand(uv, time);

    vec2 f = vec2(cos(a) * s, sin(a) * s);
    uv.x += r * f.x * p * s + IDLE_DISSOLVE_FACTOR * r * sin(-u_mouse.x);
    uv.y += r * f.y * p * s + IDLE_DISSOLVE_FACTOR * r * sin(-u_mouse.y);

    // Blend được nửa đường thì đếm tgian nhấp nháy, không nhấp nháy khi ng dùng scroll
    float flickTimer = u_flickTimer * step(0.935, blend) * step(bailProgress, 0.03);

    vec4 c = texture2D(original, uv);
    vec4 target = texture2D(target, uv);

    c = mix(c, target, flickTimer);

    vec2 n = normalize(uv.xy - f);
    float d = dot(f, n);

    if (d < 0.0) {
      c = DISSOLVE_COLOR; //c.a = 0.0;
    }

    c = mix(c, DISSOLVE_COLOR, s); //c.a = s,

    return c;
  }

  float normnoise(float noise) {
    return 0.5*(noise+1.0);
  }

  float clouds(vec2 uv) {
    uv -= vec2(0.07 * time, time*0.04);

    vec2 off1 = vec2(50.0,33.0);
    vec2 off2 = vec2(0.0, 0.0);
    vec2 off3 = vec2(-300.0, 50.0);
    vec2 off4 = vec2(-100.0, 200.0);
    vec2 off5 = vec2(400.0, -200.0);
    vec2 off6 = vec2(100.0, -1000.0);
    float scale1 = 3.0;
    float scale2 = 6.0;
    float scale3 = 12.0;
    float scale4 = 24.0;
    float scale5 = 48.0;
    float scale6 = 96.0;
    return normnoise(snoise(vec3((uv+off1)*scale1,time*0.5))*0.8 +
                      snoise(vec3((uv+off2)*scale2,time*0.4))*0.4 +
                      snoise(vec3((uv+off3)*scale3,time*0.1))*0.2 +
                      snoise(vec3((uv+off4)*scale4,time*0.7))*0.1 +
                      snoise(vec3((uv+off5)*scale5,time*0.2))*0.05 +
                      snoise(vec3((uv+off6)*scale6,time*0.3))*0.025);
  }

  void main() {
    vec2 uv = gl_FragCoord.xy / u_resolution.x;

    vec2 center = vec2(0.5,0.5*(u_resolution.y/u_resolution.x));

    vec2 light = vec2(
      0.1 + (sin(time * 2.) - 0.5) * 0.2,
      -0.5 + cos(time * 4.) * 0.5 + sin(time * 3.) * 0.3
    ) * 0.2 + center;

    light.y -= (bailProgress - 1.0) * 0.1 * step(1.001, bailProgress);

    vec3 lightColor = vec3(0.6, 0.6, 0.6);

    // vec3 lightColor = vec3((sin(time) + 0.5) / 2., 0.5, 0.5);

    float cloudIntensity = 0.7 * (1.0-(2.5*distance(uv, light)));

    gl_FragColor = vec4(vec3(cloudIntensity * clouds(uv)) * lightColor, 0.7);

    if (bailProgress < 1.0) {
      vec4 dissolvedImage = dissolve();
      gl_FragColor += dissolvedImage;
    }

    if (bailProgress > 1.0) {
      gl_FragColor.a = 2.45 - bailProgress;
    }
  }
`;
